import React,{useContext} from 'react';
import styled from 'styled-components'
import {FaCartPlus} from 'react-icons/fa'

import {Context} from './../../../components/context'
import { Link } from "react-router-dom";

const Product =(props)=>{
    const value=useContext(Context);
    return (
    <React.Fragment>
    <ProductWrapper className="col-9 mx-auto col-md-6 col-lg-3 my-3">
        <div className="card">
        <div className="img-container p-5"
             onClick={() => value.handleDetail(props.product.id)}>
          <Link to="/details">
        <img src={props.product.img} alt="" className="card-img-top" />
        </Link>
        <button className="cart-btn"
                disabled={props.product.inCart ? true : false}
                onClick={() => {
                      value.addToCart(props.product.id);
                      value.openModal(props.product.id);
                    }}
                  >
                    { props.product.inCart ? (
                      <p className="text-capitalize mb-0" disabled>
                        in cart
                      </p>
                    ) : (
                      <FaCartPlus size="30px" color="white"/>
                    )}
                  </button>
         </div>
         <div className="card-footer d-flex justify-content-between">
            <p className="align-self-center mb-0">{props.product.title}</p>
            <h5 className="text-blue font-italic mb-0">
              <span className="mr-1">$</span>
              {props.product.price}
            </h5>
          </div>
          </div>
      </ProductWrapper>
    </React.Fragment>
    );
}
export default Product;

 export const ProductWrapper = styled.div`
 .card {
  border-color: transparent;
  transition: all 1s linear;
}
.card-footer {
  background: transparent;
  border-top: transparent;
  transition: all 1s linear;
}
&:hover {
  .card {
    border: 0.04rem solid rgba(0, 0, 0, 0.2);
    box-shadow: 2px 2px 5px 0px rgba(0, 0, 0, 0.2);
  }
  .card-footer {
    background: rgba(247, 247, 247);
  }
}
.img-container {
  position: relative;
  overflow: hidden;
}
.card-img-top {
  transition: all 1s linear;
}
  .img-container:hover .card-img-top {
    transform: scale(1.2);
  }
  .cart-btn {
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem 0.4rem;
    background: #1D273C;
    border: none;
    color: white;
    font-size: 1.4rem;
    border-radius: 0.5rem 0 0 0;
    transform: translate(100%, 100%);
    transition: all 1s ease-in-out;
  }
  .img-container:hover .cart-btn {
    transform: translate(0, 0);
  }
  .cart-btn:hover {
    color: var(--mainBlue);
    cursor: pointer;
  }
`;